import { Component, NgZone, OnInit } from '@angular/core';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { URLS } from 'src/assets/constant';

@Component({
  selector: 'app-idea-history',
  templateUrl: './idea-history.page.html',
  styleUrls: ['./idea-history.page.scss'],
})
export class IdeaHistoryPage implements OnInit {
  historyData: any;
  showMedia:boolean=false;
  currentItemindex: any;
  history: any;
  constructor(private zone:NgZone,private localStorage:LocalStorageService,private apiservices:ApiservicesService,) { }

  ngOnInit() {
    this.HistoryList();
  }

  HistoryList(){
    this.apiservices.showLoader();
    this.zone.run(()=>{
      this.localStorage.getStorage().then(stored=>{
      let apiKey={  
        "employee_id":stored.employeeID
    };
      console.log("resend OTP by Voice Call, api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.ideaHistoryApi, apiKey).subscribe((result) => {
        this.apiservices.hideLoader();
        console.log("resend OTP by Voice Call, api response==",result);
        this.history=result;
        if(result.status==1 || result.status=='1'){
          this.historyData=result.data;
        }else{
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      },err=>{
        this.apiservices.hideLoader();
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
    
  });
});

  }

  showData(ind){
    this.currentItemindex = ind;
    // if(this.showMedia==true){
    //   this.showMedia=false;
    //   // item.showSegment = item.showSegment == true ? false : true;
    // }else{
    //   this.showMedia=true; 
    // }

  }

}
